@use 'base/variables' as v;
@use 'base/mixins' as m;

.lineup{
    background-color: v.$celeste;
    padding: 5rem 0;

    h3,p{
        text-align: center;
        color: v.$blanco;
    }

    .dia{
        text-transform: uppercase;
        margin: 4rem 0;
    }
}

.escenarios-contenedor{
    @include m.tablet{
        @include m.grid(2, 5rem);
    }

    p{
        color: v.$negro;
    }
}

.escenario{
    padding-top: 2rem;
    color: v.$blanco;
    .nombre-escenario{
        color: v.$blanco;
        &::after{
            content: '';
            display: block;
            width: 20rem;
            height: 10rem;
            background-size: 15rem;
            background-repeat: no-repeat;
            background-position: center center;
            margin: 2rem auto 0 auto;
        }
    }
}

.norte .nombre-escenario::after{
    background-image: url('../../build/img/icono_rock.png');
    filter: invert(100%);
}
.sur .nombre-escenario::after{
    background-image: url('../../build/img/icono_edm.png');
    filter: invert(100%);
}

.calendario{
    @include m.reset-ul;
    li{
        padding: 2.5rem;
        font-weight: 700;
        span{
            font-weight: 500;
        }
    }
}

.bg-azul{
    background-color: v.$azul;
    li:nth-child(odd){
        background-color: darken(v.$azul, 5%);
    }
}

.bg-violeta{
    background-color: v.$violeta;
    li:nth-child(odd){
        background-color: darken(v.$violeta, 5%);
    }
}