@use 'base/variables' as v;
@use 'base/mixins' as m;

.sobre-festival{
    padding: 4rem 0;

    @include m.tablet{
        @include m.grid(2, 5rem);
    }
}

.contenido-festival{
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 5rem;

    @include m.tablet{
        margin: 0;
    }

    h2{
        margin-bottom: 2rem;
    }

    .fecha{
        color: v.$celeste;
        font-weight: 700;
    }
}