@use 'base/variables' as v;
@use 'base/mixins' as m;

.header{
    background: v.$verdeNegro;
    padding: 2rem 0;
    @include m.tablet{
        &.fijo{
            top: 0;
            left: 0;
            position: fixed;
            width: 100%;
            box-shadow: .1rem .2rem .3rem v.$sombra;
            background-color: v.$verde-blur;
        }
    }

    h1{
        color: v.$blanco;
        text-align: center;

        @include m.tablet{
            margin: 0;
        }
    }

    .contenido-header{
        @include m.tablet{
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
    }
}

.navegacion-principal{
    @include m.tablet{
        display: flex;
        gap: 2rem;
    }

    a{
        display: block;
        text-align: center;
        margin-bottom: 1rem;
        color: v.$blanco;
        font-size: 2.2rem;

        @include m.tablet{
            margin:0;
        }

        &:hover{
            color: v.$celeste;
        }
    }
}

