@use 'base/variables' as v;
@use 'base/mixins' as m;

.entradas{
    padding: 5rem 0;
    h3{
        text-align: center;
        color: v.$blanco;
        margin-bottom: 2rem;
    }
    .contenedor-entradas{
        @include m.tablet{
            @include m.grid(2, 5rem);
        }
    }
}

.pase{
    .nombre-pase{
        text-align: center;
        color: v.$blanco;
        font-size: 2.2rem;
    }
    &-header, &-footer{
        padding: .5rem;
        p{
            text-align: center;
            text-transform: uppercase;
            color: v.$blanco;
            margin: 0;
        }
    }
    &-header{
        background-color: v.$celeste;
    }
    &-body{
        background-color: v.$blanco;
        ul{
            @include m.reset-ul;
            li, :nth-child(odd){
                background-color: v.$blanco;
                padding: 1rem;
                border-bottom: .1rem solid v.$gris;
            }
        }
    }
    &-footer{
        background-color: v.$azul;
        filter: saturate(150%);
        p{
            font-size: 2.4rem;
            font-weight: 700;
            letter-spacing: .2rem;
        }
    }
}

