@use 'base/variables' as v;
@use 'base/mixins' as m;

.video{
    position: relative;
    max-height: 60rem;
    min-height: 40rem;
    overflow: hidden;

    .overlay{
        position: absolute;
        width: 100%;
        height: 100%;
        background: rgb(161,8,242);
        background: linear-gradient(270deg, rgba(161,8,242,0.5) 0%, rgba(255,0,232,0.3) 100%);
        display: flex;
        align-items: center;
    }

    video{
        width: auto;
        display: block;
        @include m.desktop{
            width: 100%;
        }
    }
}

.contenido-video{
    z-index: 10;
    h2{
        color: v.$blanco;
        font-size: 4rem;
        margin: 0;

        @include m.tablet{
            font-size: 5.5rem;
        }

        @include m.desktop{
            font-size: 7rem;
        }
    }

    p{
        background-color: v.$celeste;
        display: inline-block;
        padding: 1rem 2rem;
        font-size: 1.6rem;
        margin: 0;
        color: v.$blanco;
        font-weight: 700;
        border-radius: 9999px;

        @include m.tablet{
            font-size: 2rem;
        }

        @include m.desktop{
            font-size: 2.2rem;
        }
    }
}