@use 'base/variables' as v;
@use 'base/mixins' as m;

.galeria{
    padding: 5rem 0;
    h3{
        text-align: center;
    }
}

.galeria-imagenes{
    @include m.reset-ul;

    @include m.grid(2, 2rem);

    @include m.mobile{
        @include m.grid(3, 2rem);
    }

    @include m.tablet{
        @include m.grid(4, 2rem);
    }

    @include m.desktop{
        @include m.grid(6, 2rem);
    }
}

.overlay{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    display: grid;
    place-items: center;
    background-color: rgba(34, 34, 34, .7);
    z-index: 100;

    picture{
        display: block;
        max-width: 70rem;
        border: 1rem solid v.$blanco;
    }
}

.btn-cerrar{
    width: 4rem;
    height: 4rem;
    border-radius: 50%;
    background-color: v.$blanco;
    display: grid;
    place-items: center;
    font-weight: 700;

    &:hover{
        cursor: pointer;
    }
}